import * as React from "react";
import { Carousel, Col, Row } from "react-bootstrap";
import { StaticImage } from "gatsby-plugin-image";

const Banner = () => {
    return (
        <div id="myCarousel" className="jumbotron text-center">
            <Carousel interval={null} className="slide">
                <Carousel.Item>
                    <Row>
                        <Col sm={12} xs={12} md={6}>
                            <div className="posi">
                                <div className="slide-content">
                                    <h3>Welcome to Flipflop Team</h3>
                                    <p>Our team of experienced web developers is dedicated to delivering high-quality, user-friendly websites that exceed our clients' expectations. Contact us today to see how we can help bring your vision to life.</p>
                                </div>
                            </div>
                        </Col>
                        <Col sm={12} xs={12} md={6}>
                            <div className="sliderImg">
                                <StaticImage
                                    src="../../styles/assets/images/transban7.png"
                                    loading="eager"
                                    height={300}
                                    quality={95}
                                    formats={["auto", "webp", "avif"]}
                                    alt=""
                                    style={{ marginBottom: `var(--space-3)` }}
                                />
                            </div>
                        </Col>
                    </Row>
                </Carousel.Item>
                <Carousel.Item>
                    <Row>
                        <Col sm={12} xs={12} md={6}>
                            <div className="posi">
                                <div className="slide-content">
                                    <h3>Redesign and rebuild</h3>
                                    <p>We specialize in website redesign and rebuild projects, helping businesses modernize and improve their online presence. Our team of expert developers and designers work closely with clients to create visually stunning and highly functional websites.</p>
                                </div>
                            </div>
                        </Col>
                        <Col sm={12} xs={12} md={6}>
                            <div className="sliderImg">
                                <StaticImage
                                    src="../../styles/assets/images/transban4.png"
                                    loading="eager"
                                    height={300}
                                    quality={95}
                                    formats={["auto", "webp", "avif"]}
                                    alt=""
                                    style={{ marginBottom: `var(--space-3)` }}
                                />
                            </div>
                        </Col>
                    </Row>
                </Carousel.Item>
                <Carousel.Item>
                    <Row>
                        <Col sm={12} xs={12} md={6}>
                            <div className="posi">
                                <div className="slide-content">
                                    <h3>Our team</h3>
                                    <p>Our team of skilled web developers has faced many challenges, but we always persevere and find solutions to overcome them. We are dedicated to delivering the best products and services to our clients.</p>
                                </div>
                            </div>
                        </Col>
                        <Col sm={12} xs={12} md={6}>
                            <div className="sliderImg">
                                <StaticImage
                                    src="../../styles/assets/images/transban3.png"
                                    loading="eager"
                                    height={300}
                                    quality={95}
                                    formats={["auto", "webp", "avif"]}
                                    alt=""
                                    style={{ marginBottom: `var(--space-3)` }}
                                />
                            </div>
                        </Col>
                    </Row>
                </Carousel.Item>
                {/* <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                    <span className="glyphicon glyphicon-chevron-left"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="right carousel-control" href="#myCarousel" data-slide="next">
                    <span className="glyphicon glyphicon-chevron-right"></span>
                    <span className="sr-only">Next</span>
                </a> */}
            </Carousel>
        </div>
    )
};

export default Banner;